.splash-screen {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.splash-screen-text-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #ffffff;
    padding: 1em;
    border-radius: 1em;
}

.splash-screen .loading-dot {
    font-size: 900%;
    margin-top: -0.7em;
    position: relative;
    animation: ease-in-out infinite alternate;
    animation-name: run;
    animation-duration: 1.2s;
    line-height: 1em;
}


@keyframes run {
    0% {
        left: -90px;
        color: #eee;
    }
    50% {
        color: #666;
    }
    100% {
        left: 90px;
        color: #eee;
    }
}